<template>
  <div class="searchServiceName">
    <div class="search-container">
      <span class="search-circle"></span>
      <mt-field placeholder="请输入设备名称" v-model="serviceName" class="search-input"></mt-field>
      <span class="close-btn" @click="closeSearch('close')">取消</span>
    </div>
    <div class="search-list">
      <div class="item" v-for="(item,index) in result" @click="closeSearch('needData',index)">
        <span>{{item.materialName}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  import {searchEquimp} from '@/api/materialApply'
  
  export default {
    name: "searchServiceName",
    data() {
      return {
        serviceName: '',
        result: []
      };
    },
    components: {
    
    },
    watch:{
      serviceName(newName, oldName){
        this.getInfo(newName);
      },
    },
    methods: {
      closeSearch(gotoType,selectedIndex) {
        if (gotoType == 'needData') {
          sessionStorage.setItem('serviceInfo', JSON.stringify({
            materialName: this.result[selectedIndex].materialName,
            model: this.result[selectedIndex].model,
            price: this.result[selectedIndex].price,
            unit: this.result[selectedIndex].unit,
            materialId: this.result[selectedIndex].materialId,
            stock: 1 //需求确定库存为1
          }));
        }
        this.$router.push({
          name: 'deviceApply'
        });
      },
      getInfo(name){
        searchEquimp(name).then(res => {
          this.result = res.data.list.map(item => {
            return {
              materialName: item.materialName,
              model: item.model,
              price: item.price,
              unit: item.unit,
              materialId: item.materialId,
              stock: item.stock
            };
          })
        })
      }
    }
  };
</script>

<style lang="scss">
  .searchServiceName {
    width: 100%;
    min-height: 100vh;
    font-size: .16rem;
    
    .search-container {
      position: relative;
      display: flex;
      align-items: center;
      padding-right: .12rem;
      .search-circle {
        position: absolute;
        left: .2rem;
        z-index: 99;
        border-radius: 50%;
        width: .20rem;
        height: .20rem;
        background: url("~@/assets/icon-search.png") no-repeat;
        background-size: .2rem .2rem;
      }
      
      .mint-cell {
        border-bottom: 0;
      }
      
      .search-input {
        width: 90%;
        height: .28rem;
        
        .mint-cell-value {
          position: relative;
          
          .mint-field-core {
            padding: .04rem .2rem .04rem .43rem;
            font-size: .14rem;
            background:rgba(245,245,245,1);
            border-radius: .06rem;
          }
          
          .mint-field-clear {
            position: absolute;
            right: 5px;
          }
        }
      }
      
      .close-btn {
        width: 10%;
        text-align: right;
        color: #108EE9;
      }
    }
    
    .search-list {
      padding-left: .12rem;
      .item {
        height: .6rem;
        line-height: .6rem;
        border-bottom: 1px solid #ddd;
        
        span {
          padding-left: .06rem;
        }
      }
    }
  }
</style>



